<style scoped>

.addmargin {
  margin-top: 10px;
  margin-bottom: 10px;
}

.vue-logo-back {
  background-color: black;
}

</style>

<template>

  <div class="home">
    <div class="vue-logo-back">
      <img src="../assets/logo.png" width="100px" height="100px">
    </div>
    <div class="col-md-6 centeralign">
      <p>This Page Displays a list of customers</p>
      <p>Clicking on a Card Displays the name below. This is to demonstrate passing data from parent to child component</p>
      <p>"Click for more details" Redirects to a new page which displays the customer information</p>
      <div class="card centeralign addmargin" style="width: 18rem;" v-for="customer in customerlist" :key="customer.id">
        <div class="card-body" v-on:click="setSelectedCustomer(customer.name)">
          <h5 class="card-title">{{customer.name}}</h5>
          <p class="card-text">{{customer.email}}</p>
          <p class="card-text">{{customer.phone}}</p>

          <a class="btn btn-primary" v-on:click="goToDetailsPage(customer.id)"><span style="color:white">Click for more details</span></a>
        </div>
      </div>
    </div>
    <Display v-if="selectedCustomer!=''" :selectedCustomer="selectedCustomer" />
  </div>

</template>

<script>

// @ is an alias to /src
import Display from '@/components/Display.vue'
import axios from 'axios'
const { PlayFabClient, PlayFab} = require('playfab-sdk');
PlayFab.settings.titleId = "67A0F";

function login(myCustomId, callback){
  PlayFabClient.LoginWithCustomID({
    CreateAccount: true,
    CustomId: myCustomId
  },(error, result) => {
    if(result){
      callback(result);
    }
  });
}

export default {
  name: 'Barans',
  mounted() {
    login("baransCustomId", (result) => {
      console.log("Logged in");
      this.customerlist = [{
        "id": 1,
        "name": result,
        "email": "jsmith@test.com",
        "phone": "123456789",
        "city": "bangalore",
        "state": "karnataka",
        "country": "India",
        "organization": "Company 1",
        "jobProfile": "Software Developer",
        "additionalInfo": "Has Bought a lot of products before and a high Value Customer"
      }];
    });
  },
  data() {
    return {
      customerlist: [],
      selectedCustomer: ""
    }
  },
  components: {
    Display
  }
}

</script>
